import { useCallback } from 'react';
import { useUIStore } from '../store/ui';
import { ErrorWithCode } from '../types/ui';
import { withLoadingAndError } from '../utils/apiUtils';

/**
 * Hook for making API calls with automatic loading and error handling
 * 
 * @param actionKey - Unique key for the action (used for loading and error states)
 * @returns Object with API utilities and state
 */
export const useApi = (actionKey: string) => {
  const {
    isLoading,
    getError,
    clearError,
    startAction,
    endAction,
    setGlobalLoading,
    setGlobalError,
  } = useUIStore();

  /**
   * Execute an API call with loading and error handling
   */
  const executeApiCall = useCallback(
    async <T>(
      apiCall: () => Promise<T>,
      options: {
        handleGlobalLoading?: boolean;
        handleGlobalError?: boolean;
        onSuccess?: (data: T) => void;
        onError?: (error: any) => void;
      } = {}
    ): Promise<T | null> => {
      // Set loading states
      startAction(actionKey);
      if (options.handleGlobalLoading) {
        setGlobalLoading(true);
      }

      try {
        // Execute the API call
        const result = await apiCall();
        
        // Handle success
        endAction(actionKey);
        if (options.handleGlobalLoading) {
          setGlobalLoading(false);
        }
        
        // Call success callback if provided
        if (options.onSuccess) {
          options.onSuccess(result);
        }
        
        return result;
      } catch (error: any) {
        // Create error object
        const apiError: ErrorWithCode = {
          code: error.response?.status || 'UNKNOWN_ERROR',
          message: error.response?.data?.message || error.message || 'An unknown error occurred',
          details: error.response?.data || error,
        };
        
        // End loading and set error
        endAction(actionKey, apiError);
        if (options.handleGlobalLoading) {
          setGlobalLoading(false);
        }
        
        // Set global error if needed
        if (options.handleGlobalError) {
          setGlobalError(apiError);
        }
        
        // Call error callback if provided
        if (options.onError) {
          options.onError(error);
        }
        
        return null;
      }
    },
    [actionKey, startAction, endAction, setGlobalLoading, setGlobalError]
  );

  /**
   * Create a wrapped API function with loading and error handling
   */
  const createApiCall = useCallback(
    <T, P extends any[]>(
      apiCall: (...args: P) => Promise<T>,
      options: {
        handleGlobalLoading?: boolean;
        handleGlobalError?: boolean;
        onSuccess?: (data: T) => void;
        onError?: (error: any) => void;
      } = {}
    ) => {
      return withLoadingAndError(actionKey, apiCall, options);
    },
    [actionKey]
  );

  return {
    // State
    loading: isLoading(actionKey),
    error: getError(actionKey),
    
    // Actions
    clearError: () => clearError(actionKey),
    executeApiCall,
    createApiCall,
  };
};
