import React, { useEffect, useState } from 'react';
import { Button, Card, Divider, Space, Spin, Typography, Alert, Input } from 'antd';
import { useApi } from '../hooks/useApi';
import { userApi } from '../services/api';
import { User } from '../types/auth';
import { useUIStore } from '../store/ui';

const { Title, Text } = Typography;

/**
 * Example component demonstrating the use of loading and error handling
 */
const LoadingErrorExample: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  
  // Global UI state
  const { globalLoading, globalError, clearAllErrors } = useUIStore();
  
  // API hooks for different actions
  const fetchUsersApi = useApi('fetchUsers');
  const searchUsersApi = useApi('searchUsers');
  const createUserApi = useApi('createUser');
  
  // Fetch users on component mount
  useEffect(() => {
    fetchUsers();
  }, []);
  
  // Fetch all users
  const fetchUsers = async () => {
    const result = await fetchUsersApi.executeApiCall(
      async () => await userApi.getUsers(),
      {
        handleGlobalLoading: true,
        handleGlobalError: true,
        onSuccess: (data) => {
          console.log('Users fetched successfully:', data);
        },
      }
    );
    
    if (result) {
      setUsers(result);
    }
  };
  
  // Search users (simulated)
  const searchUsers = async () => {
    const result = await searchUsersApi.executeApiCall(
      async () => {
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 1000));
        
        // Simulate filtering
        if (searchTerm.trim() === '') {
          return await userApi.getUsers();
        }
        
        const filteredUsers = (await userApi.getUsers()).filter(user => 
          user.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
          user.email.toLowerCase().includes(searchTerm.toLowerCase())
        );
        
        // Simulate error for empty results
        if (filteredUsers.length === 0) {
          throw new Error('No users found matching your search criteria');
        }
        
        return filteredUsers;
      }
    );
    
    if (result) {
      setUsers(result);
    }
  };
  
  // Create a new user (simulated)
  const createUser = async () => {
    // This is a wrapped API call that can be reused
    const createUserWithLoading = createUserApi.createApiCall(
      async (userData: Partial<User>) => {
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 1500));
        
        // Simulate validation error
        if (!userData.username || !userData.email) {
          throw new Error('Username and email are required');
        }
        
        // Simulate success
        const newUser: User = {
          id: `${Date.now()}`,
          username: userData.username,
          email: userData.email,
          name: userData.name || userData.username,
        };
        
        return newUser;
      },
      {
        handleGlobalLoading: true,
      }
    );
    
    // Execute the wrapped API call
    const newUser = await createUserWithLoading({
      username: `user_${Date.now()}`,
      email: `user_${Date.now()}@example.com`,
    });
    
    if (newUser) {
      setUsers(prevUsers => [...prevUsers, newUser]);
    }
  };
  
  // Simulate an error
  const simulateError = async () => {
    await fetchUsersApi.executeApiCall(
      async () => {
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 1000));
        
        // Throw an error
        throw new Error('This is a simulated error');
      },
      {
        handleGlobalError: true,
      }
    );
  };
  
  return (
    <Card title="Loading and Error Handling Example" style={{ width: '100%', maxWidth: 800, margin: '0 auto' }}>
      {/* Global loading and error indicators */}
      {globalLoading && (
        <div style={{ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0, background: 'rgba(255,255,255,0.7)', display: 'flex', justifyContent: 'center', alignItems: 'center', zIndex: 10 }}>
          <Spin size="large" />
        </div>
      )}
      
      {globalError && (
        <Alert
          message="Error"
          description={`${globalError.message} (Code: ${globalError.code})`}
          type="error"
          showIcon
          closable
          onClose={clearAllErrors}
          style={{ marginBottom: 16 }}
        />
      )}
      
      {/* Action buttons */}
      <Space direction="vertical" style={{ width: '100%' }}>
        <Space>
          <Button 
            type="primary" 
            onClick={fetchUsers} 
            loading={fetchUsersApi.loading}
          >
            Fetch Users
          </Button>
          
          <Button 
            danger 
            onClick={simulateError}
            loading={fetchUsersApi.loading}
          >
            Simulate Error
          </Button>
          
          <Button 
            type="default" 
            onClick={createUser}
            loading={createUserApi.loading}
          >
            Create Random User
          </Button>
        </Space>
        
        {/* Search section */}
        <Space>
          <Input 
            placeholder="Search users" 
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            style={{ width: 200 }}
          />
          
          <Button 
            type="primary" 
            onClick={searchUsers}
            loading={searchUsersApi.loading}
          >
            Search
          </Button>
        </Space>
        
        {/* Error display for search */}
        {searchUsersApi.error && (
          <Alert
            message="Search Error"
            description={searchUsersApi.error.message}
            type="warning"
            showIcon
            closable
            onClose={searchUsersApi.clearError}
          />
        )}
        
        <Divider />
        
        {/* Results */}
        <div>
          <Title level={4}>Users ({users.length})</Title>
          
          {fetchUsersApi.loading ? (
            <div style={{ textAlign: 'center', padding: 24 }}>
              <Spin />
              <div style={{ marginTop: 8 }}>Loading users...</div>
            </div>
          ) : (
            <ul>
              {users.map(user => (
                <li key={user.id}>
                  <Text strong>{user.name || user.username}</Text> ({user.email})
                  {user.role && <Text type="secondary"> - {user.role}</Text>}
                </li>
              ))}
            </ul>
          )}
        </div>
      </Space>
    </Card>
  );
};

export default LoadingErrorExample;
