import React, { useState, useEffect } from 'react';
import { LockOutlined, UserOutlined } from '@ant-design/icons';
import { Button, Image, Input, message, Divider } from 'antd';
import LayoutDashboard from './layouts/private';
import LayoutPublic from './layouts/public';
import { useAuthStore } from './store/auth';
import { LoginCredentials } from './types/auth';
import LoadingErrorExample from './components/LoadingErrorExample';

const App: React.FC = () => {
  // Get auth state and functions from the store
  const { isAuthenticated, isLoading, error, login, logout, clearError } = useAuthStore();

  // Form state
  const [credentials, setCredentials] = useState<LoginCredentials>({
    username: '',
    password: '',
  });

  // Handle input changes
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setCredentials(prev => ({ ...prev, [name]: value }));
  };

  // Handle login
  const handleLogin = async () => {

    if (!credentials.username || !credentials.password) {
      message.error('Please enter both username and password');
      return;
    }

    await login(credentials);
  };

  // Show error message if login fails
  useEffect(() => {
    if (error) {
      message.error(error);
      clearError();
    }
  }, [error, clearError]);

  // Render dashboard if authenticated, login form otherwise
  return isAuthenticated ? (
    <LayoutDashboard>
      <div>
        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 24 }}>
          <h1>Welcome to Dashboard</h1>
          <Button onClick={logout} type="primary" danger>
            Logout
          </Button>
        </div>

        <Divider />

        {/* Loading and Error handling example */}
        <LoadingErrorExample />
      </div>
    </LayoutDashboard>
  ) : (
    <LayoutPublic>
      <div className='h-[100%] flex items-center justify-center'>
        <div className='bg-white p-12 flex flex-col gap-[25px]'>
          <Image src="./logo.png" preview={false} className="max-w-[45%] m-auto" />

          <Input
            size="large"
            placeholder="Email/Username"
            prefix={<UserOutlined />}
            className="rounded-none min-w-[350px] text-[14px]"
            name="username"
            value={credentials.username}
            onChange={handleInputChange}
          />
          <Input
            size="large"
            placeholder="Password"
            prefix={<LockOutlined />}
            className="rounded-none min-w-[350px] text-[14px]"
            type="password"
            name="password"
            value={credentials.password}
            onChange={handleInputChange}
          />
          <Button
            type="primary"
            className='bg-primary mt-4'
            onClick={handleLogin}
            loading={isLoading}
          >
            ng nhp
          </Button>
        </div>
      </div>
    </LayoutPublic>
  );
};

export default App;