import { create } from 'zustand';
import { ErrorState, ErrorWithCode, LoadingState, UIState } from '../types/ui';

/**
 * Initial state for the UI store
 */
const initialState: UIState = {
  loading: {},
  errors: {},
  globalLoading: false,
  globalError: null,
};

/**
 * UI Store for managing loading states and errors
 */
export const useUIStore = create<
  UIState & {
    // Loading state actions
    setLoading: (key: string, isLoading: boolean) => void;
    setGlobalLoading: (isLoading: boolean) => void;
    resetLoading: () => void;
    
    // Error state actions
    setError: (key: string, error: ErrorWithCode | null) => void;
    setGlobalError: (error: ErrorWithCode | null) => void;
    clearError: (key: string) => void;
    clearAllErrors: () => void;
    
    // Combined actions
    startAction: (key: string) => void;
    endAction: (key: string, error?: ErrorWithCode | null) => void;
    
    // Utility methods
    isLoading: (key: string) => boolean;
    getError: (key: string) => ErrorWithCode | null;
  }
>((set, get) => ({
  ...initialState,

  /**
   * Set loading state for a specific key
   */
  setLoading: (key: string, isLoading: boolean) => {
    set((state) => ({
      loading: {
        ...state.loading,
        [key]: isLoading,
      },
    }));
  },

  /**
   * Set global loading state
   */
  setGlobalLoading: (isLoading: boolean) => {
    set({ globalLoading: isLoading });
  },

  /**
   * Reset all loading states
   */
  resetLoading: () => {
    set({ loading: {}, globalLoading: false });
  },

  /**
   * Set error for a specific key
   */
  setError: (key: string, error: ErrorWithCode | null) => {
    set((state) => ({
      errors: {
        ...state.errors,
        [key]: error,
      },
    }));
  },

  /**
   * Set global error
   */
  setGlobalError: (error: ErrorWithCode | null) => {
    set({ globalError: error });
  },

  /**
   * Clear error for a specific key
   */
  clearError: (key: string) => {
    set((state) => {
      const newErrors = { ...state.errors };
      delete newErrors[key];
      return { errors: newErrors };
    });
  },

  /**
   * Clear all errors
   */
  clearAllErrors: () => {
    set({ errors: {}, globalError: null });
  },

  /**
   * Start an action (set loading and clear error)
   */
  startAction: (key: string) => {
    const { setLoading, setError } = get();
    setLoading(key, true);
    setError(key, null);
  },

  /**
   * End an action (clear loading and optionally set error)
   */
  endAction: (key: string, error: ErrorWithCode | null = null) => {
    const { setLoading, setError } = get();
    setLoading(key, false);
    if (error) {
      setError(key, error);
    }
  },

  /**
   * Check if a specific action is loading
   */
  isLoading: (key: string) => {
    return get().loading[key] || false;
  },

  /**
   * Get error for a specific key
   */
  getError: (key: string) => {
    return get().errors[key] || null;
  },
}));

/**
 * Helper function to create error objects
 */
export const createError = (
  code: string | number,
  message: string,
  details?: any
): ErrorWithCode => ({
  code,
  message,
  details,
});

/**
 * Helper function to handle API errors
 */
export const handleApiError = (error: any): ErrorWithCode => {
  if (error.response) {
    // Server responded with an error status
    const { status, data } = error.response;
    return {
      code: status,
      message: data?.message || `Error ${status}`,
      details: data,
    };
  } else if (error.request) {
    // Request was made but no response received
    return {
      code: 'NETWORK_ERROR',
      message: 'Network error. Please check your connection.',
      details: error.request,
    };
  } else {
    // Something else happened
    return {
      code: 'UNKNOWN_ERROR',
      message: error.message || 'An unknown error occurred',
      details: error,
    };
  }
};
