import { AxiosError } from 'axios';
import { createError, handleApiError, useUIStore } from '../store/ui';
import { AppErrorCode } from '../types/ui';

/**
 * Wrapper function for API calls that handles loading states and errors
 * 
 * @param actionKey - Unique key for the action (used for loading and error states)
 * @param apiCall - The API call function to execute
 * @param options - Additional options
 * @returns A function that executes the API call with loading and error handling
 */
export const withLoadingAndError = <T, P extends any[]>(
  actionKey: string,
  apiCall: (...args: P) => Promise<T>,
  options: {
    handleGlobalLoading?: boolean;
    handleGlobalError?: boolean;
    errorMessage?: string;
    onSuccess?: (data: T) => void;
    onError?: (error: any) => void;
  } = {}
) => {
  return async (...args: P): Promise<T | null> => {
    const {
      startAction,
      endAction,
      setGlobalLoading,
      setGlobalError,
    } = useUIStore.getState();

    // Set loading states
    startAction(actionKey);
    if (options.handleGlobalLoading) {
      setGlobalLoading(true);
    }

    try {
      // Execute the API call
      const result = await apiCall(...args);
      
      // Handle success
      endAction(actionKey);
      if (options.handleGlobalLoading) {
        setGlobalLoading(false);
      }
      
      // Call success callback if provided
      if (options.onSuccess) {
        options.onSuccess(result);
      }
      
      return result;
    } catch (error: any) {
      // Handle error
      const apiError = handleApiError(error);
      
      // End loading and set error
      endAction(actionKey, apiError);
      if (options.handleGlobalLoading) {
        setGlobalLoading(false);
      }
      
      // Set global error if needed
      if (options.handleGlobalError) {
        setGlobalError(apiError);
      }
      
      // Call error callback if provided
      if (options.onError) {
        options.onError(error);
      }
      
      return null;
    }
  };
};

/**
 * Execute an API call with automatic retry on failure
 * 
 * @param apiCall - The API call function to execute
 * @param options - Retry options
 * @returns The result of the API call
 */
export const withRetry = async <T>(
  apiCall: () => Promise<T>,
  options: {
    maxRetries?: number;
    retryDelay?: number;
    shouldRetry?: (error: any) => boolean;
  } = {}
): Promise<T> => {
  const {
    maxRetries = 3,
    retryDelay = 1000,
    shouldRetry = (error) => {
      // By default, retry on network errors or 5xx server errors
      if (error.response) {
        return error.response.status >= 500;
      }
      return error.request !== undefined; // Network error
    },
  } = options;

  let lastError: any;
  
  for (let attempt = 0; attempt < maxRetries; attempt++) {
    try {
      return await apiCall();
    } catch (error) {
      lastError = error;
      
      // Check if we should retry
      if (!shouldRetry(error) || attempt === maxRetries - 1) {
        throw error;
      }
      
      // Wait before retrying
      await new Promise(resolve => setTimeout(resolve, retryDelay * (attempt + 1)));
    }
  }
  
  // This should never be reached, but TypeScript requires it
  throw lastError;
};
